#root {
  min-height: 100vh;
  background: #282a36;
}

.App {
  min-height: 100vh;
  .board-container {
    padding-top: 100px;
    text-align: center;
    min-height: 50vh;
    .board {
      display: inline-block;
      border: #f1fa8c solid 2px;
      .row {
        display: flex;
        .box {
          min-width: 10px;
          min-height: 10px;
          border: #282a36 solid 1px;
          &:hover {
            border: #f8f8f2 solid 1px;
          }
        }
      }
    }
  }
  .start-stop, .controls {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-weight: bold;
    button {
      font-weight: bold;
      padding: 16px 10px;
      margin: 0 10px;
      font-size: 14px;
      border-style: none;
      color: #282a36;
      border-radius: 5px;
      &.main {
        padding: 30px 20px;
        font-size: 20px;
        color: #282a36;
      }
    }
    .tick-rate {
      text-align: center;
      margin: 0 10px;
      color: #f8f8f2;
      min-width: 150px;
      p {
        margin: 0;
      }
      input[type=range] {
        width: 100%;
        margin: 13.8px 0;
        background-color: transparent;
        -webkit-appearance: none;
      }
      input[type=range]:focus {
        outline: none;
      }
      input[type=range]::-webkit-slider-runnable-track {
        background: #ffb86c;
        border: 0.2px solid #010101;
        border-radius: 1.3px;
        width: 100%;
        height: 8.4px;
        cursor: pointer;
      }
      input[type=range]::-webkit-slider-thumb {
        margin-top: -14px;
        width: 16px;
        height: 36px;
        background: #50fa7b;
        border: 1px solid #000000;
        border-radius: 3px;
        cursor: pointer;
        -webkit-appearance: none;
      }
      input[type=range]:focus::-webkit-slider-runnable-track {
        background: #ffc78b;
      }
      input[type=range]::-moz-range-track {
        background: #ffb86c;
        border: 0.2px solid #010101;
        border-radius: 1.3px;
        width: 100%;
        height: 8.4px;
        cursor: pointer;
      }
      input[type=range]::-moz-range-thumb {
        width: 16px;
        height: 36px;
        background: #50fa7b;
        border: 1px solid #000000;
        border-radius: 3px;
        cursor: pointer;
      }
      input[type=range]::-ms-track {
        background: transparent;
        border-color: transparent;
        border-width: 14.8px 0;
        color: transparent;
        width: 100%;
        height: 8.4px;
        cursor: pointer;
      }
      input[type=range]::-ms-fill-lower {
        background: #ffa94d;
        border: 0.2px solid #010101;
        border-radius: 2.6px;
      }
      input[type=range]::-ms-fill-upper {
        background: #ffb86c;
        border: 0.2px solid #010101;
        border-radius: 2.6px;
      }
      input[type=range]::-ms-thumb {
        width: 16px;
        height: 36px;
        background: #50fa7b;
        border: 1px solid #000000;
        border-radius: 3px;
        cursor: pointer;
        margin-top: 0px;
        /*Needed to keep the Edge thumb centred*/
      }
      input[type=range]:focus::-ms-fill-lower {
        background: #ffb86c;
      }
      input[type=range]:focus::-ms-fill-upper {
        background: #ffc78b;
      }
      /*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
      how to remove the virtical space around the range input in IE*/
      @supports (-ms-ime-align:auto) {
        /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
        input[type=range] {
          margin: 0;
          /*Edge starts the margin from the thumb, not the track as other browsers do*/
        }
      }
    }
  }
}


